import React from 'react'
import './index.css'
import loadingImg from '../../common/img/wheel/1.png'
import scrapeImg from '../../common/img/wheel/2.png'
import albxImg from '../../common/img/wheel/3.png'
import smallcx from '../../common/img/wheel/4.png'
import czymf from '../../common/img/wheel/5.png'
export default function Essay() {
    const img=[
        {
            img:loadingImg,
            title:'Loading旋转效果',
            url1:'http://aays.top/wheel/loading/index.html',
            url2:'https://github.com/Escaay?tab=repositories'
        },
        {
            img:scrapeImg,
            title:'puppeteer爬虫',
            url1:'http://aays.top/wheel/scrape/index.html',
            url2:'https://github.com/Escaay?tab=repositories'
        },
        {
            img:albxImg,
            title:'bootstrap响应式开发',
            url1:'http://aays.top/wheel/albx/index.html',
            url2:'https://github.com/Escaay?tab=repositories'
        },
        {
            img:smallcx,
            title:'美味山苏微信小程序',
            url1:'http://aays.top/wheel/smallcx/index.html',
            url2:'https://github.com/Escaay?tab=repositories'
        },{
            img:czymf,
            title:'创造与魔法(php)',
            url1:'http://aays.top/wheel/czymf/index.php',
            url2:'https://github.com/Escaay?tab=repositories'
        },
    ]
    return (
        <div className='essay'>
            <div className='essay-img'>
            {
                img.map((item,index)=>{
                    return (
                        <div className='img-box'>
<img src={item.img} className='img-item'/>
<div className='img-title'>
    {item.title}
</div>
<div className='img-bottom'>
    <a href={item.url1}><span>成品展示</span></a>
    <span> | </span>
    <a href={item.url2}><span>代码地址</span></a>
</div>
</div>
                    )
                })
            }
        </div>
        </div>
    )
}
